<template>
  <div class="interactive">
    <div class="in-button helpful">
      <van-icon size="34" name="thumb-circle-o"/>
    </div>
    <div class="in-button useless">
      <van-icon size="34" name="clear" />
    </div>
    <div class="in-button whatsapp">
      <van-icon size="34" color="#07c160" name="phone-circle" />
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import { Icon } from 'vant';

Vue.use(Icon);

export default {
  name: 'Interactive',
};
</script>

<style scoped>
.interactive{
  display: flex;
  margin-top: 5px;
}
.in-button{
  margin-right: 5px;
}
</style>



