<template>
  <div class="chat">
    <Header/>
    <ChatList ref="chat"/>
    <QuickQuestion @handleShowDetail="appendDialog"/>
  </div>
</template>

<script>
import Header from '@/components/Header';
import ChatList from '@/components/ChatList';
import QuickQuestion from '@/components/QuickQuestion';

export default {
  name: 'Chat',
  components: {
    Header,
    ChatList,
    QuickQuestion,
  },
  data() {
    return {
      msg: 'Welcome to Your Vue.js App',
    };
  },
  methods: {
    appendDialog(id) {
      this.$refs.chat.appendDialog(id);
    },
  },
};
</script>



