<template>
  <div class="quick-question">
    <div>我想问</div>
    <div class="question" @click="showDetail(1)">提现</div>
    <div class="question" @click="showDetail(2)">提现方式</div>
    <div class="question" @click="showDetail(4)">绑定手机</div>
  </div>
</template>

<script>
import Avatar from '@/assets/avatar.png';
import Hello from '@/assets/hello.json';
import ScrollMagic from 'scrollmagic';

export default {
  name: 'Header',
  data() {
    return {
      name: 'Ronny Chieng',
      avatar: Avatar,
      quickQuestionArr: Hello.questions[1].answer,
    };
  },
  methods: {
    showDetail(id) {
      if (id) {
        this.$emit('handleShowDetail', id);
        this.$nextTick(() => {
          const chatDialog = document.querySelectorAll('.chat-dialog');
          const controller = new ScrollMagic.Controller();
          controller.scrollTo(function (newpos) {
            TweenMax.to(window, 0.5, { scrollTo: { y: newpos } })
          });
          controller.scrollTo(chatDialog[chatDialog.length - 2]);
        });
      }
    },
  },
};
</script>

<style scoped>
.quick-question{
  display: flex;
  height: 35px;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #ececec;
  align-items: center;
  padding: 0 15px;
  box-sizing: border-box;
  z-index: 1000;
  font-size: 12px;
}
.question{
  font-size: 10px;
  border: 1px solid #ccc;
  height: 20px;
  border-radius: 15px;
  padding: 0 10px;
  margin-left: 10px;
  line-height: 20px;
}
</style>



