<template>
  <div class="chat-dialog" :class="isCustomer ? 'chat-customer' : ''">
    <div class="chat-avatar"></div>
    <div class="chat-content">
      <div class="title">{{ data.question }}</div>
      <div class="question-list" v-if="data.answer">
        <div class="question"
          v-for="(item, index) in data.answer"
          :key="index"
          @click="showDetail(item.id)"
        >
          <span v-if="item.type === 'text'">{{item.content}}</span>
          <span class="link-style" v-if="item.type === 'link'">{{item.content}}</span>
          <img :src="item.content" v-if="item.type === 'image'" />
        </div>
      </div>
      <div class="more"
        v-if="data.id && data.answer.length > 3"
        @click="showPopup(data.id)"
      >
        全屏查看>>
      </div>
      <ChatInterative v-if="data.id"/>
    </div>
  </div>
</template>

<script>
import ChatInterative from '@/components/ChatInteractive';
import ScrollMagic from 'scrollmagic';
import { TweenMax, ScrollToPlugin } from "gsap/all";

export default {
  name: 'Header',
  props: {
    isCustomer: {
      type: Boolean,
      default: false,
    },
    data: {
      type: Object,
      default: function () {
        return {};
      },
    },
  },
  components: {
    ChatInterative,
  },
  data() {
    return {
      name: 'Ronny Chieng',
    };
  },
  methods: {
    showDetail(id) {
      if (id) {
        this.$emit('handleShowDetail', id);
        this.$nextTick(() => {
          const chatDialog = document.querySelectorAll('.chat-dialog');
          const controller = new ScrollMagic.Controller();
          controller.scrollTo(function (newpos) {
            TweenMax.to(window, 0.5, { scrollTo: { y: newpos } });
          });
          controller.scrollTo(chatDialog[chatDialog.length - 2]);
        });
      }
    },
    showPopup(id) {
      if (id) {
        this.$emit('handleShowPopup', id);
      }
    },
  },
};
</script>

<style scoped>
.chat-dialog{
  display: flex;
  justify-content: flex-start;
  width: 100%;
}
.chat-dialog.chat-customer{
  flex-direction: row-reverse;
}
.chat-content{
  position: relative;
  line-height: 22px;
  padding: 10px 15px;
  background-color: #eee;
  border-radius: 5px;
  color: #333;
  word-break: break-all;
  font-size: 16px;
  margin-left: 15px;
  max-width: 280px;
  box-sizing: border-box;
}
.chat-customer .chat-content{
  margin-right: 15px;
  margin-left: 0;
  text-align: left;
  background-color: #33df83;
  color: #fff;
}
.chat-content:after {
  content: "";
  position: absolute;
  left: -10px;
  top: 15px;
  width: 0;
  height: 0;
  border-style: solid dashed dashed;
  border-color: #eee transparent transparent;
  overflow: hidden;
  border-width: 10px;
}
.chat-customer .chat-content:after {
    left: auto;
    right: -10px;
    border-top-color: #33df83;
}
.chat-avatar{
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-image: url('../assets/avatar.png');
  background-size: 30px;
  overflow: hidden;
}
.chat-customer .chat-avatar{
  background-image: url('../assets/customer-avatar.png');
}
.chat-avatar img{
  width: 30px;
  height: 30px;
  display: block;
}
.question span.link-style{
  color: #1989fa;
  /* color: #333; */
  text-decoration: underline;
}
.question img{
  width: 100%;
  margin: 8px 0;
}
.more{
  color: #1989fa;
  /* color: #333; */
  text-decoration: underline;
}
</style>



