<template>
  <div class="chat-detail" :id="data.id">
    <div class="chat-content">
      <div class="title">{{ data.question }}</div>
      <div class="question-list" v-if="data.answer">
        <div class="question"
          v-for="(item, index) in data.answer"
          :key="index"
        >
          <span v-if="item.type === 'text'">{{item.content}}</span>
          <span class="link-style" v-if="item.type === 'link'">{{item.content}}</span>
          <img :src="item.content" v-if="item.type === 'image'" />
        </div>
      </div>
      <ChatInterative v-if="data.id"/>
    </div>
  </div>
</template>

<script>
import ChatInterative from '@/components/ChatInteractive';

export default {
  name: 'Header',
  props: {
    data: {
      type: Object,
      default: function () {
        return {};
      },
    },
  },
  components: {
    ChatInterative,
  },
};
</script>

<style scoped>
.chat-detail{
  display: flex;
  justify-content: flex-start;
  width: 100%;
}
.chat-content{
  position: relative;
  line-height: 22px;
  padding: 44px 15px 10px;
  border-radius: 5px;
  color: #333;
  word-break: break-all;
  font-size: 16px;
  box-sizing: border-box;
}
.question img{
  width: 100%;
  margin: 8px 0;
}
</style>



