<template>
  <div class="chatlist">
    <van-list>
      <div class="chat-dialogs" v-for="(item, index) in dialogArr" :key="index">
        <ChatDialog
          :isCustomer="item.isCustomer"
          :data="item"
          @handleShowDetail="appendDialog"
          @handleShowPopup="showPopup"
        />
      </div>
    </van-list>
    <van-popup
      v-model="show"
      round
      closeable
      position="bottom"
      :style="{ height: '90%' }"
    >
      <ChatDetail :data="questions[id - 1]"/>
    </van-popup>
  </div>
</template>

<script>
import Vue from 'vue';
import { List, Popup } from 'vant';
import ChatDialog from '@/components/ChatDialog';
import ChatDetail from '@/components/ChatDetail';
import Question from '@/assets/question.json';
import Hello from '@/assets/hello.json';

Vue.use(Popup);
Vue.use(List);

export default {
  name: 'Chatlist',
  components: {
    ChatDialog,
    ChatDetail,
  },
  data() {
    return {
      loading: false,
      finished: false,
      questions: Question.questions,
      dialogArr: Hello.questions,
      show: false,
      id: null,
    };
  },
  methods: {
    appendDialog(id) {
      this.dialogArr.push({
        isCustomer: true,
        question: this.questions[id - 1].question,
      }); // 纯粹演示用，真实数据的时候需要引入lodash
      this.dialogArr.push(this.questions[id - 1]);
    },
    showPopup(id) {
      this.id = id;
      this.show = true;
    },
  },
};
</script>

<style scoped>
.chatlist{
  margin: 50px 0 35px;
  padding: 10px;
  box-sizing: border-box;
}
.chat-dialogs{
  margin-bottom: 15px;
}
</style>



