<template>
  <div class="header">
    <img :src="avatar">
    <div class="avatar">{{ name }}</div>
  </div>
</template>

<script>
import Avatar from '@/assets/avatar.png';

export default {
  name: 'Header',
  data() {
    return {
      name: 'Ronny Chieng',
      avatar: Avatar,
    };
  },
};
</script>

<style scoped>
.header{
  display: flex;
  height: 50px;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background-color: #ececec;
  align-items: center;
  padding: 0 15px;
  box-sizing: border-box;
  z-index: 1000;
}
.header img{
  width: 30px;
  height: 30px;
  border-radius: 50%;
  /* padding: 2px; */
  border: 1px solid #fff;
}
.avatar {
  margin-left: 5px;
}
</style>



